<template>
  <div class="fixed-container">
    <div class="main-grid">
      <logo-vue :logo="logo"/>
      <static-block 
        :value="companyName"
        name="Company Name"
        :gridStyle="{'grid-column': '1/2'}"
      />

      <static-block 
        :link="link"
        name="Your Link:"
        :gridStyle="{'grid-column': '2/4'}"
      />

      <div class="input-block" :style="{'grid-column': '1/2 '}">
        <p class="input-block__title">Media about company</p>
        <div  
          class="input-block__main input-block__main_grid" 
          ref="holder_1" 
          tabindex="1"
          @click="bringParentFocus('holder_1')"
            v-click-away="onClickAway"
          >
          <div
              class="file-input file-input_img" 
              v-for="file in files" 
              :key="file"
              :style="{'background': `url(${file}) center`, 'background-size': 'cover'}"
            >
              <button 
                type="button" 
                @click="removeFile(file)"
                class="file-input__close"
                >
                x
              </button>

              <!-- <img 
                alt="file" 
                :src="file"
                class="file-input__img"
                /> -->
          </div>
          
          <div v-if="files.length < 4" class="file-input">            
            <input type="file" 
              @focus="bringParentFocus('holder_1')" 
              @blur="bringParentBlur('holder_1')"
              @change="addFiles($event)"
            />
            <div class="file-input__plus">+</div>
          </div>
        </div>
      </div>
      
      <div class="input-block"  :style="{'grid-column': '2/6 '}">
        <p class="input-block__title">About:</p>
        <div class="input-block__main" ref="holder_2" tabindex="2">
          <div class="textarea-input">
            <textarea 
            @focus="bringParentFocus('holder_2')" 
            @blur="bringParentBlur('holder_2')"
            ref="textarea"
            cols="60" rows="14" 
            v-bind:value="content">
          </textarea>
          </div>
        </div>
      </div>

      <div class="input-block" :style="{'grid-column': '1/2 '}">
        <p class="input-block__title">Categories</p>
        <div class="input-block__main" ref="holder_3" >
          <ul
            tag="ul"
            tabindex="3" 
            @focus="bringParentFocus('holder_3')" 
            @blur="bringParentBlur('holder_3')"
            ref="categories"
            class="input-list"
            :style="{'height': `${toggleItemsList.length * 30 + 40}px`, 'min-height': '130px'}"
            >
              <li v-for="item in toggleItemsList" :key="item + Math.random()" >
                <span 
                  @click="showAllList && toggleSelectCategories(item)" 
                  :class="(selectedItems.includes(item) && showAllList) && 'active' || (selectedItems.length > 2 && showAllList) && 'disabled'"
                  >
                  {{item}}
                </span>
              </li>
            </ul>
        </div>
      </div>

      <div class="input-block"  :style="{'grid-column': '2/3 '}">
        <p class="input-block__title">Shipping Options</p>
        <div class="input-block__main" ref="holder_4" >
          <ul 
            tabindex="4" 
            @focus="bringParentFocus('holder_4')" 
            @blur="bringParentBlur('holder_4')" 
            class="input-list" 
            >
              <li>Canada Post - 2days - 14$</li>
              <li>Israili Post - 22days - 140$</li>
          </ul>
        </div>
      </div>

      <div class="buttons" :style="{'grid-column': '5/6 '}">
        <a class="btn" href="#">Products</a>
        <a class="btn" href="#">Orders</a>
      </div>

    </div>
  </div>
</template>

<script>
import LogoVue from './components/Logo.vue';
import StaticBlock from './components/UI/StaticBlock.vue';
export default {
  name: 'App',
  components: {
    LogoVue, StaticBlock
  },
  data() {
    return {
      itemsList: ['Arts and Crafts' , 'Bags', 'Clothes', 'Cosmetic', 'Sport and Fitness', 'Toys and Games', 'Jewelry', 'Judaica', 'Medical','Nutrition','Textile','Services'],
      selectedItems: [],
      companyName: '',
      link: '',
      content: '',
      files: [],
      showAllList: false,
      logo: ''
    }
  },
  computed: {
    toggleItemsList() {
      return this.showAllList ? this.itemsList : this.selectedItems
    }
  },
  methods: {
    toggleSelectCategories(item) {
      if (!this.selectedItems.includes(item) && this.selectedItems.length < 3) {
        this.selectedItems.push(item)
      } else {
        const items = this.selectedItems.filter(i => i !== item)
        this.selectedItems = items
      }
    },
    addFiles(e) {
      const file = e.target.files[0];
      const url = URL.createObjectURL(file);
      this.files.push(url)
    },
    removeFile(file) {
      this.files = this.files.filter(item => item !== file)
    },
    onClickAway() {
      this.bringParentBlur('holder_1')
    },
    bringParentFocus(holder) {
      switch(holder) {
        case ('holder_1'): 
          this.$refs.holder_1.className = this.$refs.holder_1.className + ' input-block__main_focus';
          break;
        case ('holder_2'): 
          this.$refs.holder_2.className = this.$refs.holder_2.className + ' input-block__main_focus';
          this.$refs.textarea.className = 'textarea_open';
          setTimeout(() => {
            this.$refs.textarea.rows = 20;
          }, 300)
          break;
        case ('holder_3'): 
          this.$refs.holder_3.className = this.$refs.holder_3.className + ' input-block__main_focus';
          this.$refs.categories.className = this.$refs.categories.className + ' input-list_open';
          setTimeout(() => {
            this.showAllList = true;
          }, 300)
          break;
        case ('holder_4'): 
          this.$refs.holder_4.className = this.$refs.holder_4.className + ' input-block__main_focus';
          break;
        default: break
      }
    },
    bringParentBlur(holder) {
      switch(holder) {
        case ('holder_1'): 
          this.$refs.holder_1.className = this.$refs.holder_1.className.split('input-block__main_focus')[0];
          break;
        case ('holder_2'): 
          this.$refs.holder_2.className = this.$refs.holder_2.className.split('input-block__main_focus')[0];
          this.$refs.textarea.className = '';
          setTimeout(() => {
            this.$refs.textarea.rows = 14;
          }, 300)
          break;
        case ('holder_3'): 
          this.$refs.holder_3.className = this.$refs.holder_3.className.split('input-block__main_focus')[0];
          this.$refs.categories.className = this.$refs.categories.className.split('input-list_open')[0];
          this.showAllList = false;
          break;
        case ('holder_4'): 
          this.$refs.holder_4.className = this.$refs.holder_4.className.split('input-block__main_focus')[0];
          break;
        default: break
      }
    }
  },
  mounted() {
    fetch('/data.json')
    .then((res) => res.json())
    .then(res => {
      this.companyName = res.company_name;
      this.link = res.link;
      this.content = res.content;
      this.selectedItems = res.select_items;
      this.logo = res.logo
    })
  }
}
</script>

